.success {
    color: green;
    background: #a9f0d1;
    font-size: 20px;
    border: 2px solid;
    border-radius: 5px;
    padding: 5px;
    margin-bottom: 10px
}
.error {
    color: #5b2333;
    background: #e6bccd;
    font-size: 20px;
    border: 2px solid;
    border-color: #5b2333;
    border-radius: 5px;
    padding: 5px;
    margin-bottom: 10px
}
